.App {
  display: flex;
  flex-wrap: nowrap;
  height: 100vh;
  width: 100vw;
}

.MessageComposeContainer {
  padding: 2em;
  background-color: #e3eee3;
  flex-basis: 80%;
  display: flex;
  flex-direction: column;
}

.MessageComposeContainer h1 {
  flex-basis: 5%;
  padding-bottom: 1em;
}

h1 {
  font-size: 1.5em;
  margin: 0;
}





.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
