.MessageList ul {
    list-style: none;
    padding: 0 0.5em;
}

.timeStamp{
    white-space: nowrap;
    margin-right: 2em;
}

.MessageList p {
    display: inline; 
    word-break: break-all;
}


.MessageList li{
    display: flex;
}


.StatusMessage{
    font-weight: bold;
    color: #8d718b;
}

.JoinMessage{
    font-weight: bold;
}

.PartMessage{
    font-weight: bold;
}